body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen and (max-width: 768px) {
    .none-mobile{
      display: none !important;
    }

    .none-desktop{
      display: inherit !important;
    }
}

@media only screen and (min-width: 769px) {
  .none-mobile{
    display: inherit !important;
  }

  .none-desktop{
    display: none !important;
  }
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

html, body {
    height: 100%;
    overflow: hidden;
}

#root{
    height: 100%;
}

.full-height, .App, .carousel-inner, .carousel-item, img{
    height: 100%;
}
.navigation{
    height: 3.5em;
    background-color: white;
    margin-left: 0px !important;
    z-index: 1001;
}
.navigation-logo-container{
    margin-left: -3em !important;
    width: calc(25% + 3em);
    height: 100%;
    position: relative;
}

.navigation-logo-background{
    position: absolute;
    background-color: #0B5C3B;
    width: 100%;
    -webkit-transform: skewX(-30deg);
    transform: skewX(-30deg);
    z-index: 0;
    height: 100%;
}

.navigation-logo{
    position: absolute;
    right: 50px;
    height: 100%;
    z-index: 1;
}

.navigation-action-menu{
    width: 8em;
    height: 100%;
    position: relative;
}

.navigation-action-menu-background{
    position: absolute;
    background-color: #0B5C3B;
    width: 100%;
    right: -30px;
    -webkit-transform: skewX(-30deg);
    transform: skewX(-30deg);
    z-index: 0;
    height: 100%;
}

.navigation-action-menu-icon{
    position: absolute;
    cursor: pointer;
    right: 1em;
    height: 100%;
}

.navigation-action-icon{
    /* width: 3em;
    height: 3em; */
    color: #aaa;
    font-size: 1.5em;
    cursor: pointer;
}

.active{
    color:  #0B5C3B;
}

.navigation-right-container{

}

.navigation-link{
    color: white;
}
.navigation-link:hover{
    text-decoration: none;
    color: #0B5C3B;
}

@media only screen and (max-width: 768px) {
    .navigation{
        height: 3.5em;
        background-color: #0B5C3B;
    }

    .navigation-logo-container{
        margin-left: 8px !important;
        /* width: 50%; */
        background-color: #0B5C3B;
        border: white solid;
        border-width: 0px;
        border-right-width: 1px;
    }

    .navigation-logo{
        right: 4px;
        height: 70%;
    }

    .navigation-logo-background{
        -webkit-transform: skewX(0deg);
        transform: skewX(0deg);
    }

    .navigation-right-container{
        width: 49.5% !important;
        background-color:  #0B5C3B;
    }

    .navigation-action-menu{
        width: 3em;
        margin-right: 8px;
        height: 70% !important;
    }
    
    .navigation-action-menu-background{
        right: 0px;
        -webkit-transform: skewX(0deg);
        transform: skewX(0deg);
    }
    
    .navigation-action-menu-icon{
        position: absolute;
        right: 0.25em;
    }

    .navigation-action-icon{
        font-size: 1.25em;
        color: white;
    }
  }

.navigation-menu-overlay{
    width: 0px;
    background-color: rgba(0,0,0,0.3);
    -webkit-transition: width .5s ease-in-out;
    transition: width .5s ease-in-out;
    z-index: 1000;
    right: 0;
}

.navigation-menu-container{
    margin-top: 3.5em;
    width: 300px;
    background-color: #aaa;
    overflow: scroll;
}

.navigation-menu-item{
    border: white solid 0px;
    border-bottom-width: 1px;
    text-align: end;
    background-color: gray;
    text-transform: uppercase;
    cursor: pointer;
}
.paginationBox {
    width: 40px;
    height: auto;
    position: absolute;
    -webkit-transform: translate(0, -50%);
    transform: translate(0, -50%);
    background-color: #a3a3a3;
    right: 0;
    top: 50%;
    padding: 5px 0;
    z-index: 17;
}

.paginationBox ul {
    width: 100%;
    text-align: center;
    padding-left: 0px;
    margin-bottom: 0px;
    /* position: relative; */
}

.paginationBox li {
    display: inline-block;
    width: 40px;
    height: 40px !important;
    margin: 0;
    cursor: pointer;
    color: white;
    position: relative;
    height: auto;
    cursor: pointer;
    /* padding: 10px 0px; */
}

.paginationBox li a {
    display: block;
    widows: 100%;
    height: auto;
    font-size: 18px;
    text-transform: uppercase;
    line-height: 40px;
    text-align: center;
    font-family: "montserrat", "san-serif";
    font-weight: 300;
}

.paginationBox li.currentIndex a {
    width: 0;
    color: #fff;
}

.paginationBox li.currentIndex a:after, .paginationBox li:hover a:after {
    width: 30px;
}

.paginationBox li a:after{
    content: "";
    width: 0;
    height: 1px;
    background-color: #fff;
    left: 5px;
    margin: 0;
    -webkit-transition: width .3s ease-in-out;
    transition: width .3s ease-in-out;
    z-index: 1;
    position: absolute;
    top: 50%;
}

@-webkit-keyframes changeColorText {
    0% {color:#a3a3a3;}
    100% {color: #a3a3a3;}
}

@keyframes changeColorText {
    0% {color:#a3a3a3;}
    100% {color: #a3a3a3;}
}

.currentIndex a span{
    color: #a3a3a3;
}
.paginationBox li:hover a span{
    color: #a3a3a3;
}
.paginationBox .noneIndex a span{
    -webkit-animation: changeColorText 0.3s;
            animation: changeColorText 0.3s;
}


