.real-val-model{
    width: calc(100vh - 250px);
    height: calc(100vh - 250px);
    position: relative;
    display: block;
    margin: auto;
    top: 35px;
}
.real-val-cycle-container,.real-val-cycle, .real-val-cycle ul {
    width: 100%;
    height: 100%;
    position: relative;
    display: block;
    margin: auto;
    list-style-type: none;
}

.real-val-cycle::after{
    content: '';
    width: 80%;
    height: 80%;
    position: absolute;
    left:10%;
    top: 10%;
    border: 12px solid #0B5C3B;
    border-radius: 50%;
    z-index: -1;
    margin:auto
}

.real-val-cycle li::before{
    content: "";
    height: 100%;
    display: inline-block;
    vertical-align: middle;
}
.real-val-cycle li{
    background-image: url(data:image/png;base64,);
    background-size: 90%;
    background-position: center;
    background-repeat: no-repeat;
    width: 26%;
    height: 26%;
    position: absolute;
    z-index: 2;
    border-radius: 50%;
    border: solid 2px #0B5C3B;
    cursor: pointer;
    color: #fff ;
    -webkit-transition: all .5s;
    transition: all .5s;
}

.real-val-cycle li:hover{
    border: solid 2px #fff;
}

.real-val-cycle li a{
    height: auto;
    width: 100%;
    margin: 0 -5px;
    font-size: calc(1rem + ((1vw - .6rem) * 1.5));
    display: inline-block;
    padding: 20px;
    text-transform: uppercase;
    font-weight: 700;
}

.real-val-cycle li:nth-child(1){
    left: 37%;
    top: -5%
}
.real-val-cycle li:nth-child(2){
    left: 77%;
    top: 20%
}
.real-val-cycle li:nth-child(3){
    right: 10%;
    bottom: 5%
}
.real-val-cycle li:nth-child(4){
    right: 64%;
    bottom: 5%
}
.real-val-cycle li:nth-child(5){
    left: -3%;
    top: 20%
}


.real-val-cycle-bgr{
    /* background-color: #fff; */
    height: 50%;
    width: 50%;
    position: absolute;
    left: 25%;
